import React, { useState } from 'react';
import Tesseract from 'tesseract.js';

function OCR() {
  const [selectedFile, setSelectedFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [parsedData, setParsedData] = useState(null);

  const handleFileChange = (event) => {
    setSelectedFile(event.target.files[0]);
    console.log("Archivo seleccionado:", event.target.files[0]);
  };

  const handleFileUpload = () => {
    if (selectedFile) {
      setLoading(true);
      console.log("Procesando archivo...");
      Tesseract.recognize(
        selectedFile,
        'eng',
        {
          logger: (m) => console.log(m),
        }
      ).then(({ data: { text } }) => {
        console.log("Texto extraído:", text);
        const parsedData = parseFlightDetails(text);
        console.log("Datos parseados:", parsedData);
        setParsedData(parsedData);
        setLoading(false);
      }).catch((error) => {
        console.error("Error en la extracción OCR:", error);
        setLoading(false);
      });
    }
  };

  const parseFlightDetails = (text) => {
    // Divide el texto en líneas
    const lines = text.split('\n').map(line => line.trim()).filter(line => line !== "");
    console.log("Líneas extraídas:", lines);

    // Estructura para almacenar los detalles de los vuelos
    const flights = [];
    let currentFlight = {};

    lines.forEach((line, index) => {
      if (line.startsWith('CM')) {
        if (Object.keys(currentFlight).length > 0) {
          flights.push(currentFlight);
        }
        currentFlight = { flightNumber: line };
      } else if (index % 5 === 1) {
        currentFlight.class = line;
      } else if (index % 5 === 2) {
        currentFlight.cities = line;
      } else if (index % 5 === 3) {
        currentFlight.depart = line;
      } else if (index % 5 === 4) {
        currentFlight.arrive = line;
      }
    });
    if (Object.keys(currentFlight).length > 0) {
      flights.push(currentFlight);
    }

    return flights;
  };

  return (
    <div>
      <h2>OCR para Extraer Información de Imagen</h2>
      <input type="file" onChange={handleFileChange} />
      <button onClick={handleFileUpload} disabled={!selectedFile || loading}>
        {loading ? 'Procesando...' : 'Subir'}
      </button>
      <div>
        <h3>Detalles del Vuelo:</h3>
        {parsedData && parsedData.map((flight, index) => (
          <div key={index}>
            <p><strong>Número de Vuelo:</strong> {flight.flightNumber}</p>
            <p><strong>Clase:</strong> {flight.class}</p>
            <p><strong>Ciudades:</strong> {flight.cities}</p>
            <p><strong>Salida:</strong> {flight.depart}</p>
            <p><strong>Llegada:</strong> {flight.arrive}</p>
            <hr />
          </div>
        ))}
      </div>
    </div>
  );
}

export default OCR;
